import {__} from '@wordpress/i18n';

import {
    PanelBody,
    BaseControl,
    SelectControl,
    RangeControl,
    ToggleControl,
    Button,
    TextControl,
    TextareaControl,
    Dashicon,
    Tip,
    Disabled,
    __experimentalBoxControl as BoxControl,
} from '@wordpress/components';

import {
    MediaUploadCheck,
    MediaUpload,
    PanelColorSettings,
    InspectorControls,
    useBlockProps,
} from '@wordpress/block-editor';

import ServerSideRender from '@wordpress/server-side-render';

export default function Edit(props) {

    const {attributes, setAttributes} = props;

    const {
        heading,
        headingHTMLTag,
        tocAdded,
        headingColor,
        darkHeadingColor,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        description,
        descriptionColor,
        darkDescriptionColor,
        desktopDescriptionSize,
        tabletDescriptionSize,
        mobileDescriptionSize,
        metaScore,
        meta,
        overlayMetaColor,
        overlayMetaBg,
        price,
        salePrice,
        priceCurrency,
        offerUntil,
        priceColor,
        darkPriceColor,
        desktopPriceSize,
        tabletPriceSize,
        mobilePriceSize,
        image,
        imageURL,
        imageAlt,
        productFeatures,
        prosLabel,
        productPros,
        consLabel,
        productCons,
        buyButtons,
        buttonColor,
        buttonBg,
        darkButtonColor,
        darkButtonBg,
        starColor,
        isBorderButtonColor,
        isBorderButtonBorder,
        isBorderDarkButtonColor,
        isBorderDarkButtonBg,
        desktopButtonSize,
        tabletButtonSize,
        mobileButtonSize,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        background,
        darkBackground,
        desktopPadding,
        tabletPadding,
        mobilePadding,
        isMainReview
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    function setMediaAttribute(name, value) {
        setAttributes({[name]: value.url});
        setAttributes({[name + 'Alt']: value.alt});
    }

    function removeMediaAttribute(name) {
        setAttributes({[name]: ''});
        setAttributes({[name + 'Alt']: ''});
    }

    /** repeated */
    const setRepeatedAttribute = (attributeName, index, name, value) => {
        const data = [...attributes[attributeName]];
        data[index] = {
            ...data[index],
            [name]: value
        };
        setAttributes({[attributeName]: data});
    };

    function addRepeatedAttribute(attributeName, defaultValues) {
        const data = [...attributes[attributeName]];
        data.push(defaultValues);
        setAttributes({[attributeName]: data});
    }

    const removeRepeatedAttribute = (attributeName, index) => {
        const data = [...attributes[attributeName]];
        data.splice(index, 1);
        setAttributes({[attributeName]: data});
    };

    const borderStyleOptions = [
        {label: __('None'), value: 'none'},
        {label: __('Solid'), value: 'solid'},
        {label: __('Dashed'), value: 'dashed'},
        {label: __('Dotted'), value: 'dotted'},
        {label: __('Double'), value: 'double'},
    ];

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    return (
        <div {...useBlockProps()}>
            <InspectorControls>
                <PanelBody title={__('Product Image')} initialOpen={true}>
                    <BaseControl>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setMediaAttribute('image', value)}
                                allowedTypes={['image']}
                                value={image}
                                render={({open}) => (
                                    <div>
                                        {!image && (
                                            <Button className="button button-large" onClick={open}>
                                                {__('Add Image')}
                                            </Button>
                                        )}
                                        {image && (
                                            <div>
                                                <img src={image} alt="image"/>
                                                <Button className="button button-large" onClick={() => removeMediaAttribute('image')}>
                                                    {__('Remove Image')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </BaseControl>
                    <TextareaControl
                        label={__('or Enter Custom Link')}
                        value={imageURL}
                        onChange={(value) => setAttribute('imageURL', value)}
                        placeholder={__('//website.com/.../image.jpg')}
                    />
                </PanelBody>
                <PanelBody title={__('Details')} initialOpen={true}>
                    <TextareaControl
                        label={__('Product Name')}
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        placeholder={__('Input your product title...')}
                    />
                    <SelectControl
                        label={__('Heading Tag')}
                        value={headingHTMLTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingHTMLTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                    <TextareaControl
                        label={__('Summary Review')}
                        value={description}
                        onChange={(value) => setAttribute('description', value)}
                        placeholder={__('Input your product description or summary review...')}
                    />
                    <TextControl
                        label={__('Meta')}
                        placeholder="Good"
                        value={meta}
                        onChange={(value) => setAttribute('meta', value)}
                        help={__('The meta label request product image has been added in order to work.')}
                    />
                    <TextControl
                        label={__('Fallback Average Score')}
                        value={metaScore}
                        onChange={(value) => setAttribute('metaScore', value)}
                        placeholder= "5"
                        help={__('This value is shown as a fallback when no product criteria data exists, within the range of 0 to 5.')}
                    />
                    <TextControl
                        label={__('Price')}
                        placeholder="$99"
                        value={price}
                        onChange={(value) => setAttribute('price', value)}
                        help={__('Including the currency symbol in your price.')}
                    />
                    <TextControl
                        label={__('Discounted Price')}
                        placeholder="$49"
                        value={salePrice}
                        onChange={(value) => setAttribute('salePrice', value)}
                    />
                    <TextControl
                        label={__("Advantage Label")}
                        value={prosLabel}
                        onChange={(value) => setAttribute('prosLabel', value)}
                    />
                    <TextControl
                        label={__("Disadvantage Label")}
                        value={consLabel}
                        onChange={(value) => setAttribute('consLabel', value)}
                    />
                    <TextControl
                        label={__('Currency Code')}
                        placeholder="USD"
                        value={priceCurrency}
                        onChange={(value) => setAttribute('priceCurrency', value)}
                        help={__('This value for the schema markup. Currency code in three digit ISO 4217 code')}
                    />
                    <TextControl
                        label={__('Price Valid Until')}
                        placeholder="yyyy-mm-dd"
                        value={offerUntil}
                        onChange={(value) => setAttribute('offerUntil', value)}
                        help={__('Input the valid until date for this offer, Ensure you input right format: yyyy-mm-dd')}
                    />
                </PanelBody>
                <PanelBody title={__('Criteria Data')} initialOpen={false}>
                    {productFeatures.map((item, index) => (
                        <PanelBody key={index} title={item.label || __('+ Criteria...')} initialOpen={false} className="rb-repeated-settings">
                            <TextControl
                                label={__("Label")}
                                value={item.label}
                                onChange={(value) => setRepeatedAttribute('productFeatures', index, 'label', value)}
                            />
                            <RangeControl
                                label={__("Rating")}
                                value={item.rating}
                                onChange={(value) => setRepeatedAttribute('productFeatures', index, 'rating', value)}
                                initialPosition={5}
                                min={0}
                                step={0.1}
                                max={5}
                            />
                            <div className="rb-repeated-remove">
                                <Button variant="secondary" isSmall onClick={() => removeRepeatedAttribute('productFeatures', index)}>
                                    <Dashicon icon="trash"/> {__("Delete")}
                                </Button>
                            </div>
                        </PanelBody>
                    ))}
                    <div className="rb-repeated-add">
                        <Button variant="primary" onClick={() => addRepeatedAttribute('productFeatures', {
                            label: "",
                            rating: ""
                        })}>
                            {__("Add New Criteria")}
                        </Button>
                    </div>
                </PanelBody>
                <PanelBody title={__('Advantages')} initialOpen={false}>
                    {productPros.map((item, index) => (
                        <PanelBody key={index} title={`+ ${item.content}` || __('Please input pros...')} initialOpen={false} className="rb-repeated-settings">
                            <TextareaControl
                                label={__("Content")}
                                value={item.content}
                                onChange={(value) => setRepeatedAttribute('productPros', index, 'content', value)}
                            />
                            <div className="rb-repeated-remove">
                                <Button variant="secondary" isSmall onClick={() => removeRepeatedAttribute('productPros', index)}>
                                    <Dashicon icon="trash"/> {__("Delete")}
                                </Button>
                            </div>
                        </PanelBody>
                    ))}
                    <div className="rb-repeated-add">
                        <Button variant="primary" onClick={() => addRepeatedAttribute('productPros', {
                            content: ""
                        })}>
                            {__("Add New")}
                        </Button>
                    </div>
                </PanelBody>
                <PanelBody title={__('Disadvantages')} initialOpen={false}>
                    {productCons.map((item, index) => (
                        <PanelBody key={index} title={`- ${item.content}` || __('Please input cons...')} initialOpen={false} className="rb-repeated-settings">
                            <TextareaControl
                                label={__("Content")}
                                value={item.content}
                                onChange={(value) => setRepeatedAttribute('productCons', index, 'content', value)}
                            />
                            <div className="rb-repeated-remove">
                                <Button variant="secondary" isSmall onClick={() => removeRepeatedAttribute('productCons', index)}>
                                    <Dashicon icon="trash"/> {__("Delete")}
                                </Button>
                            </div>
                        </PanelBody>
                    ))}
                    <div className="rb-repeated-add">
                        <Button variant="primary" onClick={() => addRepeatedAttribute('productCons', {
                            content: ""
                        })}>
                            {__("Add New")}
                        </Button>
                    </div>
                </PanelBody>
                <PanelBody title={__('Affiliate Links')} initialOpen={false}>
                    {buyButtons.map((item, index) => (
                        <PanelBody key={index} title={item.label || __('Affiliate Link...')} initialOpen={false} className="rb-repeated-settings">
                            <TextControl
                                label={__("Label")}
                                value={item.label}
                                onChange={(value) => setRepeatedAttribute('buyButtons', index, 'label', value)}
                                placeholder="By on Amazon"
                            />
                            <TextControl
                                label={__("Link")}
                                value={item.link}
                                onChange={(value) => setRepeatedAttribute('buyButtons', index, 'link', value)}
                                placeholder="https://..."
                            />
                            <ToggleControl
                                label={__("Sponsored?")}
                                checked={item.isSponsored}
                                onChange={(value) => setRepeatedAttribute('buyButtons', index, 'isSponsored', value)}
                            />
                            <ToggleControl
                                label={__("Border Style")}
                                checked={item.isButtonBorder}
                                onChange={(value) => setRepeatedAttribute('buyButtons', index, 'isButtonBorder', value)}
                            />
                            <div className="rb-repeated-remove">
                                <Button variant="secondary" isSmall onClick={() => removeRepeatedAttribute('buyButtons', index)}>
                                    <Dashicon icon="trash"/> {__("Delete")}
                                </Button>
                            </div>
                        </PanelBody>
                    ))}
                    <div className="rb-repeated-add">
                        <Button variant="primary" onClick={() => addRepeatedAttribute('buyButtons', {
                            label: "",
                            link: "",
                            isButtonBorder: false,
                            isSponsored: false
                        })}>
                            {__("Add New Link")}
                        </Button>
                    </div>
                </PanelBody>
                <PanelBody title={__('Is Post Review?')} initialOpen={false}>
                    <ToggleControl
                        label={__('Set it as the main review')}
                        help={__('Use this review as the main review of this post. Enabling this will make this post appear in the review filters.')}
                        checked={isMainReview}
                        onChange={(value) => setAttribute('isMainReview', value)}
                    />
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Heading')} initialOpen={false}>
                    <label className="rb-control-label">{__('Heading Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: headingColor,
                                onChange: (value) => setAttribute('headingColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkHeadingColor,
                                onChange: (value) => setAttribute('darkHeadingColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Description')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopDescriptionSize}
                                onChange={(value) => setAttribute('desktopDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletDescriptionSize}
                                onChange={(value) => setAttribute('tabletDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileDescriptionSize}
                                onChange={(value) => setAttribute('mobileDescriptionSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: descriptionColor,
                                onChange: (value) => setAttribute('descriptionColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkDescriptionColor,
                                onChange: (value) => setAttribute('darkDescriptionColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Price')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopPriceSize}
                                onChange={(value) => setAttribute('desktopPriceSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletPriceSize}
                                onChange={(value) => setAttribute('tabletPriceSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobilePriceSize}
                                onChange={(value) => setAttribute('mobilePriceSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: priceColor,
                                onChange: (value) => setAttribute('priceColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkPriceColor,
                                onChange: (value) => setAttribute('darkPriceColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Review Meta')} initialOpen={false}>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: starColor,
                                onChange: (value) => setAttribute('starColor', value),
                                label: __('Star Color'),
                            },
                            {
                                value: overlayMetaColor,
                                onChange: (value) => setAttribute('overlayMetaColor', value),
                                label: __('Overlay Meta - Color'),
                            },
                            {
                                value: overlayMetaBg,
                                onChange: (value) => setAttribute('overlayMetaBg', value),
                                label: __('Overlay Meta - Background'),
                            },

                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Affiliate Links')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopButtonSize}
                                onChange={(value) => setAttribute('desktopButtonSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletButtonSize}
                                onChange={(value) => setAttribute('tabletButtonSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileButtonSize}
                                onChange={(value) => setAttribute('mobileButtonSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Background Button Style')}
                        colorSettings={[
                            {
                                value: buttonColor,
                                onChange: (value) => setAttribute('buttonColor', value),
                                label: __('Color'),
                            },
                            {
                                value: buttonBg,
                                onChange: (value) => setAttribute('buttonBg', value),
                                label: __('Background'),
                            },
                            {
                                value: darkButtonColor,
                                onChange: (value) => setAttribute('darkButtonColor', value),
                                label: __('Dark Mode - Color'),
                            },
                            {
                                value: darkButtonBg,
                                onChange: (value) => setAttribute('darkButtonBg', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                    <PanelColorSettings
                        title={__('Border Button Style')}
                        colorSettings={[
                            {
                                value: isBorderButtonColor,
                                onChange: (value) => setAttribute('isBorderButtonColor', value),
                                label: __('Color'),
                            },
                            {
                                value: isBorderButtonBorder,
                                onChange: (value) => setAttribute('isBorderButtonBorder', value),
                                label: __('Border Color'),
                            },
                            {
                                value: isBorderDarkButtonColor,
                                onChange: (value) => setAttribute('isBorderDarkButtonColor', value),
                                label: __('Dark Mode - Color'),
                            },
                            {
                                value: isBorderDarkButtonBg,
                                onChange: (value) => setAttribute('isBorderDarkButtonBg', value),
                                label: __('Dark Mode - Border Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Border & Shadow')} initialOpen={false}>
                    <SelectControl
                        label={__('Border Style')}
                        value={borderStyle}
                        options={borderStyleOptions}
                        onChange={(value) => setAttribute('borderStyle', value)}
                        help={__('Please select the border color to see the border if you enable it.')}
                    />
                    {borderStyle && borderStyle !== 'none' && (
                        <>
                            <BoxControl
                                resetValues={{top: null, right: null, bottom: null, left: null}}
                                label={__('Border Width')}
                                values={borderWidth}
                                onChange={(value) => setAttribute('borderWidth', value)}
                            />
                            <PanelColorSettings
                                title={__('Border Color')}
                                colorSettings={[
                                    {
                                        value: borderColor,
                                        onChange: (value) => setAttribute('borderColor', value),
                                        label: __('Color'),
                                    },
                                    {
                                        value: darkBorderColor,
                                        onChange: (value) => setAttribute('darkBorderColor', value),
                                        label: __('Dark Mode - Color'),
                                    },
                                ]}
                            />
                        </>
                    )}
                    <RangeControl
                        label={__('Border Radius')}
                        value={borderRadius}
                        onChange={(value) => setAttribute('borderRadius', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <ToggleControl
                        label={__('Shadow')}
                        checked={shadow}
                        onChange={(value) => setAttribute('shadow', value)}
                    />
                    <PanelColorSettings
                        title={__('Background')}
                        colorSettings={[
                            {
                                value: background,
                                onChange: (value) => setAttribute('background', value),
                                label: __('Background'),
                            },
                            {
                                value: darkBackground,
                                onChange: (value) => setAttribute('darkBackground', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Inner Padding')} initialOpen={false}>
                    <BaseControl><Tip>{__('These settings will apply to block wrapper. Click outside to see the update!')}</Tip></BaseControl>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '30px', right: '30px', bottom: '30px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop')}</>}
                            values={desktopPadding}
                            onChange={(value) => setAttribute('desktopPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '25px', right: '25px', bottom: '25px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet')}</>}
                            values={tabletPadding}
                            onChange={(value) => setAttribute('tabletPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '20px', right: '20px', bottom: '20px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile')}</>}
                            values={mobilePadding}
                            onChange={(value) => setAttribute('mobilePadding', value)}
                        />
                    </div>
                </PanelBody>
            </InspectorControls>
            <Disabled>
                <ServerSideRender
                    block="foxiz-elements/review"
                    attributes={attributes}
                />
            </Disabled>
        </div>
    )
}
